}
#team .member h4 {
  font-weight: 700;
  margin-bottom: 2px;
  font-size: 18px;
  color: #fff;
}
#team .member span {
  font-style: italic;
  display: block;
  font-size: 13px;
  color: #fff;
}
#team .member .social {
  margin-top: 15px;
}
#team .member .social a {
  transition: 0.3s;
  color: rgba(255, 255, 255, 0.7);
}
#team .member .social a:hover {
  color: #fff;
}
#team .member .social i {
  font-size: 18px;
  margin: 0 2px;
}
#gud img {
