  .faq .faq-left-desktop {
    display: block;
   
  }
  .faq .faq-left-mobile {
    display: none;
   
  }
}
@media (max-width: 600px) {
  body {
    height: auto;
  }
  section {
    padding: 160px 0px 80px;
  }
  .faq {
    width: 100%;
    height: auto;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: unset;
    top: unset;
    left: unset;
    transform: unset;
  }
  .faq .faq-right {
    width: 100%;
    padding: 25px;
    align-items: center;
  }
  .faq .faq-right .faq-title {
